@property --rotate {
  syntax: "<angle>";
  initial-value: 132deg;
  inherits: false;
}

:root {
  --card-height: 65vh;
  --card-width: calc(var(--card-height) / 1.5);
}

.card {
  background: #191c29;
  width: var(--card-width);
  height: var(--card-height);
  padding: 3px;
  position: relative;
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  text-align: center;
  display: flex;
  flex-direction: column;
  font-size: 1.5em;
  color: rgb(88 199 250 / 100%);
  cursor: pointer;
  font-family: cursive;
  margin: 3vw 10vh;
  padding-top: 3vmax;
}

.card:hover {
  /* color: rgb(88 199 250 / 100%); */
  transition: color 1s;
}
/* .card:hover:before,
.card:hover:after {
  animation: none;
  opacity: 0;
} */

.card::before {
  content: "";
  width: 104%;
  height: 102%;
  border-radius: 8px;
  background-image: linear-gradient(
    var(--rotate),
    #5ddcff,
    #3c67e3 43%,
    #4e00c2
  );
  position: absolute;
  z-index: -1;
  top: -1%;
  left: -2%;
  animation: spin 2.5s linear infinite;
}

.card::after {
  position: absolute;
  content: "";
  top: calc(var(--card-height) / 6);
  left: 0;
  right: 0;
  z-index: -1;
  height: 100%;
  width: 100%;
  margin: 0 auto;
  transform: scale(0.8);
  filter: blur(calc(var(--card-height) / 6));
  background-image: linear-gradient(
    var(--rotate),
    #5ddcff,
    #3c67e3 43%,
    #4e00c2
  );
  opacity: 1;
  padding: 1vamx;
  transition: opacity 0.5s;
  animation: spin 2.5s linear infinite;
}

.userMessages {
  width: var(--card-width);
  overflow-y: auto;
}
.userMessages::-webkit-scrollbar {
  width: 6px;
}

.userMessages::-webkit-scrollbar-thumb {
  background-color: #666;
  border-radius: 5px;
}

.userMessages::-webkit-scrollbar-track {
  background-color: #333;
}
.message-container {
  line-height: 2px;
}

.message-container:hover {
  border: 1px solid #ccc;
  padding: 3px;
  margin-bottom: 5px;
  background-color: #f9f9f9;
  border-radius: 5px;
  box-shadow: 0 2px 4px rgba(8, 149, 243, 0.596);
  height: fit-content;
}

.message-container h4 {
  font-size: 14px;
  color: #555;
}
.message-container i {
  font-size: 14px;
  color: #555;
  margin: 10px;
}

.message-container p {
  font-size: 12px;
  color: #333;
}

.contactBox {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
  margin: 0 auto;
}

@keyframes spin {
  0% {
    --rotate: 0deg;
  }
  100% {
    --rotate: 360deg;
  }
}
